<li>The <code v-pre="">vc-measure-distance</code> component is used for distance measurement.</li>
<li>The <code v-pre="">vc-measure-area</code> component is used for area measurement.</li>
<li>The <code v-pre="">vc-measure-height</code> component is used for high measurement.</li>
</ul>
<h2>Example</h2>
<h3>Measuring distance, area, height</h3>
