        <vc-measure-distance :clamp-to-ground="clampToGround" ref="measureDistance" @activeevt="activeEvt" @measureevt="measureEvt" @movingevt="movingEvt" :remove-last-position="removeLastPosition"></vc-measure-distance>
        <vc-measure-area ref="measureArea" @activeevt="activeEvt" @measureevt="measureEvt" @movingevt="movingEvt" :clamp-to-ground="clampToGround" :remove-last-position="removeLastPosition"></vc-measure-area>
